<template>
  <!-- /signing -->  
  <!-- html and js autoinjects to App.vue (and therefore on public/index.html) -->
  <v-container>
    <Header />
    <Timedate />
    <div id="errordiv" v-if="errors.length">
      <ul class="collection with-header" style="border:1px solid red;">
        <li class="collection-header" style="background: rgba(0,0,0,0);">
          <v-alert type="error">
            Korjaa seuraavat virheet:
          </v-alert>
        </li>
        <li class="collection-item" id="error" v-for="(error,index) in errors" v-bind:key="index">
          <p class="flow-text">{{ index+1}}. {{ error }}</p>
        </li>
      </ul>
    </div>

    <div class="container-transparent">
      <div class="section">
          <div class="col s12 center-align"><h1>Ilmoittautuminen</h1></div>
      </div>
      <!-- Navigation to other competition pages -->
      <v-row>
        <v-row>
          <v-col order="first">
            <router-link to="/comp-settings">
              <v-btn large rounded color="grey" class="white--text"><i class="material-icons left">tune</i>Mritykset</v-btn>
            </router-link>
          </v-col>
          <v-col>
            <router-link to="/overview">
              <v-btn large rounded color="primary"><i class="material-icons left">info</i>Kilpailun yleisnkym</v-btn>
            </router-link>
          </v-col> 
          <v-col  order="last">
            <router-link to="/weighting">
              <v-btn large rounded color="blue darken-4" class="white--text"><i class="material-icons left">fitness_center</i>Punnitus</v-btn>
            </router-link>
          </v-col>
        </v-row>
      </v-row>
      <!-- Tabs -->

      <v-tabs
        v-model="tab"
        background-color="blue lighten-2"
        color="basil"
        grow
      >
        <v-tabs-slider color="blue darken-4"></v-tabs-slider>
        <v-tab href="#signing">Ilmoittautuminen</v-tab>
        <v-tab href="#signees">Ilmoittautuneet</v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab" style="background: rgba(0,0,0,0.4);">
        <!-- Signing tab -->
        <v-tab-item class="inputarea" :value="'signing'">
          <v-row v-if="!loading_site">
            <v-col>
              <v-row style="margin-top:20px;">
                <v-col md="10" offset-md="1">
                  <p class="flow-text">Ilmoittautuminen</p>  
                </v-col>
              </v-row>

              <v-row v-if="notification">
                <v-col md="8" offset-md="2">
                  <v-alert type="info" class="flow-text">
                    {{ notification }}
                  </v-alert>
                </v-col>
              </v-row>
              <!-- If there user is trying to overwrite data to other boat number, which is in use, show differences and ask are you sure -->
              <v-row v-if="old_info">
                <v-col md="8" offset-md="2">
                  <h3>Numeron ({{ old_info.boat_number }}.) Vanhat tiedot</h3>
                  <p class="flow-text"><b>Kapteeni:</b> {{ old_info.captain_name }}</p>
                  <p class="flow-text"><b>Varakapteeni:</b> {{ old_info.temp_captain_name }}</p>
                  <p class="flow-text"><b>Seura/Paikkakunta:</b> {{ old_info.locality }}</p>
                  <p class="flow-text"><b>Lhtpaikka:</b> {{ old_info.starting_place }}</p> 
                  <p v-if="isTeamCompetition" class="flow-text"><b>Joukkue:</b> {{ old_info.team }}</p>   
                </v-col>

                <v-col md="8" offset-md="2">
                  <v-divider class="black"></v-divider>
                </v-col>

                <v-col md="8" offset-md="2">
                  <h3>Numeron ({{ boat_number }}.) Uudet tiedot</h3>
                  <p class="flow-text"><b>Kapteeni:</b> {{ captain_name }}</p>
                  <p class="flow-text"><b>Varakapteeni:</b> {{ temp_captain_name }}</p>
                  <p class="flow-text"><b>Seura/Paikkakunta:</b> {{ locality }}</p>
                  <p class="flow-text"><b>Lhtpaikka:</b> {{ starting_place }}</p> 
                  <p v-if="isTeamCompetition" class="flow-text"><b>Joukkue:</b> {{ team }}</p>  
                </v-col>

                <v-col md="8" offset-md="2" style="margin-bottom:50px">
                  <v-divider class="black"></v-divider>
                </v-col>
                <v-row>
                  <v-col order="first">
                    <v-btn large tile color="yellow" @click="overwriteSignee(old_info, false)"><i class="material-icons left">backspace</i>Peruuta</v-btn>
                  </v-col>
                  <v-col order="last">
                    <v-btn large tile color="green" @click="overwriteSignee(old_info, true)"><i class="material-icons left">check</i>Pllekirjoita</v-btn>
                  </v-col>
                </v-row>
              </v-row>
              <!-- Inputs -->
              <v-row v-if="!old_info" id="signing-inputs">
                <v-col>
                  <v-row>
                    <v-col md="3" offset-md="3" class="input-fields">
                      <v-text-field
                        label="Venekunnan numero"
                        v-model="boat_number"
                        append-outer-icon="add"
                        maxlength="6"
                        @click:append-outer="boat_number > 0 ? boat_number++ : boat_number = 1" 
                        prepend-icon="remove" 
                        @click:prepend="boat_number > 1 ? boat_number-- : boat_number = 1"
                        @paste.prevent
                        :counter="6"
                        @keypress="isNumber($event, true)"
                        :rules="number_rules"
                        :loading="loading"
                      />
                    </v-col>
                  
                    <v-col md="2">
                      <v-btn large color="blue" @click="searchFromCup"><i class="material-icons left">find_replace</i>Hae cupista</v-btn>
                    </v-col>

                    <v-col md="2">
                      <v-btn large color="indigo" @click="searchSelected"><i class="material-icons left">find_replace</i>Hae ilmoittautuneista</v-btn>
                    </v-col>
                  </v-row> 

                  <v-row>
                    <v-col md="5" offset-md="3" class="input-fields">
                      <v-text-field
                        label="Kapteeni"
                        v-model="captain_name"
                        :maxlength="maxlength"
                        :loading="loading"
                        :counter="maxlength"
                      />
                    </v-col>
                  </v-row>

                  <v-row>
                    <v-col md="5" offset-md="3" class="input-fields">
                      <v-text-field
                        label="Varakapteeni"
                        v-model="temp_captain_name"
                        :maxlength="maxlength"
                        :loading="loading"
                        :counter="maxlength"
                      />
                    </v-col>
                  </v-row>

                  <v-row>
                    <v-col md="5" offset-md="3" class="input-fields">
                      <v-text-field
                        label="Seura/Paikkakunta"
                        v-model="locality"
                        id="locality"
                        :maxlength="maxlength"
                        :loading="loading"
                        :counter="maxlength"
                      />
                    </v-col>
                  </v-row>

                  <v-row>
                    <v-col md="5" offset-md="3" class="input-fields">
                        <v-text-field
                          label="Lhtpaikka"
                          v-model="starting_place"
                          id="starting_place"
                          :maxlength="maxlength"
                          :loading="loading"
                          :counter="maxlength"
                        />
                    </v-col>
                  </v-row>
                  <!-- Ask team only if isTeamCompetition is true -->
                  <v-row v-if="isTeamCompetition">
                    <v-col md="5" offset-md="3" class="input-fields">
                      <div class="input-fields">
                        <vue-select
                          class="flow-text title"
                          taggable
                          placeholder="Valitse, tai kirjoita tiimin nimi"
                          v-model="team"
                          :options="teams"
                        />
                        <span class="flow-text black-text">Joukkue</span>
                      </div>
                    </v-col>
                  </v-row>

                  <v-row v-if="!old_info">
                    <v-row v-if="loading">
                      <v-col md="12">
                        <p class="flow-text">Pivitetn tiedot tietokantaan...</p>
                        <ProgressBarQuery />
                      </v-col>
                    </v-row>

                    <v-row v-else style="margin:40px">
                      <v-row>
                        <v-col
                          md="4"
                        >
                          <v-btn large tile color="red" @click="deleteSignee(false, -1)"><i class="material-icons left">delete_forever</i>Poista Venekunta</v-btn>
                        </v-col>
                        <v-col
                          md="4"
                        >
                          <v-btn large tile color="yellow" @click="clearInputs"><i class="material-icons left">backspace</i>Pyyhi Kentt</v-btn>
                        </v-col>
                        <v-col
                          md="4"
                        >
                          <v-btn large tile color="green" @click="validateInfo" id="sbtn"><i class="material-icons right">save_alt</i>Tallenna</v-btn>
                        </v-col>
                      </v-row>
                    </v-row>
                  </v-row>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
          <v-row v-else>
            <!-- Show progressbar if loading -->
            <v-col>
              <h2>Haetaan veneit...</h2>
              <ProgressBarQuery /> 
            </v-col>
          </v-row>
        </v-tab-item>
        <v-tab-item class="inputarea" :value="'signees'">
          <v-row>
            <v-col>
              <v-row style="margin-top:20px;">
                <v-col class="title" offset-md="4" md="4">
                  <p class="flow-text">Ilmoittautuneita yhteens: <b>{{ signees.length }}</b></p>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="10" offset-md="1" class="scroll_table">
                  <table id="signees-table" class="highlight centered responsive-table table_header tablearea" v-if="signees.length">
                    <thead>
                      <tr>
                        <th>Nro.</th>
                        <th>Kapteeni</th>
                        <th>Varakapteeni</th>
                        <th>Seura/Paikkakunta</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr @click="selectRow(signee.id)" :class="{ selected : selected_id == signee.id }" v-for="(signee, index) in signees" :key="index">
                          <th class="center-align" style="border:1px solid black">{{ signee.boat_number }}</th>  
                          <td style="border:1px solid black">{{ signee.captain_name }}</td> 
                          <td style="border:1px solid black">{{ signee.temp_captain_name }}</td>
                          <td style="border:1px solid black">{{ signee.locality }}</td>   
                      </tr>
                      </tbody>
                  </table>
                </v-col>
              </v-row>
              <v-row v-if="selected_id">
                <v-col style="margin:20px 50px;">
                  <v-btn large tile color="blue" @click="searchSelected"><i class="material-icons left">info</i>Nyt valitun ilmoittautumistiedot</v-btn>
                </v-col>
              </v-row>
              <v-row v-else>
                <v-col style="margin:20px 50px;">
                  <p v-if="signees.length" class="flow-text">Voit katsella venekunnan tietoja mys klikkaamalla haluamaasi rivi taulukosta ja painamalla ilmestyv nappulaa</p>
                  <h3 v-else class="center-align">Ei ilmoittautuneita!</h3>
                </v-col>
              </v-row>    
            </v-col>    
          </v-row>
        </v-tab-item>
      </v-tabs-items>
    </div>
  </v-container>
</template>
<script>
    "use strict"
    import Timedate from '../components/layout/Timedate';
    import Header from "../components/layout/Header";
    import ProgressBarQuery from '../components/layout/ProgressBarQuery';
    import M from 'materialize-css';
    import CompetitionService from '../CompetitionService';
    import CupService from '../CupService';
    import "vue-select/dist/vue-select.css";

    export default {
        name: 'Signing',
        components: {
          Timedate,
          Header,
          ProgressBarQuery
        },
        data() {
          return {
              errors: [],
              tab: null,
              boat_number: null,
              starting_place: null,
              captain_name: null,
              temp_captain_name: null,
              locality: null,
              team: null,
              selected_id:null,
              id: 1,
              new_signee:null,
              notification: null,
              loading: false,
              loading_site: false,
              old_info: null,
              signees: [],
              cup: [],
              teams: [],
              maxlength: 40,
              isTeamCompetition: false,
              number_rules: [
                value => !isNaN((value || '')) || 'Ei ole numero!',
                value =>  (value || '') >= 0  || 'Numeron pit olla positiivinen!',
              ],
          }
        },
        mounted() {
          M.AutoInit();
          this.checkLogin();
          if(localStorage.getItem('competition') != null) {
              this.refreshCompetition(localStorage.getItem('competition'));
          }

          // Focus on top of the page when changing pages
          location.href = "#";
          location.href = "#app";
        },
        methods: {
            // Fetch competition from database, and update variables
            async refreshCompetition(competition_id) {
              this.loading_site = true;

              try {
                let competitions = await CompetitionService.getCompetition(competition_id);
                if(competitions.length){
                    // Update to vuex, Assing variables and arrays from vuex (see client/store/index.js)
                    this.$store.commit('refreshCompetition', competitions[0]);
                    this.isTeamCompetition = this.$store.getters.isTeamCompetition;
                    this.signees =  this.$store.getters.getSignees;
                    this.teams = this.$store.getters.getTeams;
                    // Get max values and assign them
                    if (this.signees.length) {
                        this.boat_number = Math.max.apply(Math, this.signees.map(function(o) { return o.boat_number; })) + 1;
                        this.id = Math.max.apply(Math, this.signees.map(function(o) { return o.id; })) + 1;
                    }
                    else {
                      // Otherwise init with 1
                      this.boat_number = 1;
                      this.id = 1;
                    }
                    // Fetch cup info, to check signee from cups signees array
                    let cups = await CupService.getCup(competitions[0].cup_id);
                    if(cups.length) {
                      this.cup = cups[0];
                    }
                }
                else {
                  console.log("No competition found on database...");
                }
              } catch(err) {
                console.log(err.message);
              }
              this.loading_site = false;
            },
            //Check if user is logged in has admin status, update values to vuex (Header.vue updates based on these values)
            checkLogin: function() {
                // If login token present --> user is logged in
                if(localStorage.getItem('jwt') != null){
                    this.$store.state.logged_in = true;
                    let user = JSON.parse(localStorage.getItem('user'));
                    // Check if user is admin
                    //TODO safer way to check this than use localstorage?
                    user.is_admin == true ? this.$store.state.is_admin = true : this.$store.state.is_admin = false;
                }
                else {
                    //Not logger in, so not admin either
                    this.$store.state.logged_in = false;
                    this.$store.state.is_admin = false;
                }
            },
            // Check if input value is number, and only accept numbers to inputs
            isNumber: function(evt) {
                evt = (evt) ? evt : window.event;
                var charCode = (evt.which) ? evt.which : evt.keyCode;
                if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                  evt.preventDefault();
                } else {
                  if(charCode == 46) {
                    evt.preventDefault();
                  }
                  else {
                    return true;
                  }
                }
            },
            capitalize_words: function(str) {
                return str.replace(/\w\S*/g, function(txt){return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();});
            },
            // Clear all selections and inputs and errors
            clearInputs: function() {
                this.boat_number = null;
                this.starting_place = null;
                this.captain_name = null;
                this.temp_captain_name = null;
                this.locality = null;
                this.team = null;
                this.errors = [];
                this.old_info = null;
                this.notification = "";
            },
            // Handle overwriting user data based on user answer
            overwriteSignee: function(signee, overwrite) {
                overwrite === true ? console.log("Overwrite!") : console.log("Don't overwrite!")
                // User chose to overwrite data
                if(overwrite) {
                    // Remove old data
                    this.$store.commit("removeSignee", signee);
                    // Replace the old existing signee info with new info, without changing the id
                    signee.boat_number = parseInt(this.boat_number);
                    signee.starting_place = this.starting_place;
                    signee.captain_name = this.capitalize_words(this.captain_name);
                    signee.temp_captain_name = this.capitalize_words(this.temp_captain_name);
                    signee.locality = this.capitalize_words(this.locality);
                    signee.team = this.team;
                    // Shorten team name if over 40 characters
                    if (this.team.length > 40) {
                        let temp_team = this.team;
                        temp_team = temp_team.slice(0, 40);
                        this.team = temp_team; // Set the name back, shortened
                        this.showError("Valitse tiimin nimeksi alle 40 merkki pitk nimi (nimi lyhennetty 40 merkkiin)!");
                    }
                    else {
                      //If the array doensn't have the team name already, add it
                      if(this.teams.indexOf(this.team) == -1) {
                        this.teams.push(this.team);
                        this.$store.commit('setTeams', this.teams);
                      }
                      // Save signee to database and clear inputs
                      this.saveToDatabase(signee, true);
                      this.clearInputs();
                      this.boat_number = Math.max.apply(Math, this.signees.map(function(o) { return o.boat_number; })) + 1;
                      this.notification = "Tiedot korvattu uusilla!";
                    }

                }
                // Don't overwrite, add to new place
                else {
                  this.notification = "Veneen numero asetettu ensimmiseen vapaaseen paikkaan!";
                  this.boat_number = Math.max.apply(Math, this.signees.map(function(o) { return o.boat_number; })) + 1;
                  this.old_info = null;
                }
            },
            // If user selected from input/table, search from array and assing values to inputs
            searchSelected: function() {
                this.notification = null;
                this.errors = [];
                // Searched from Signing tab, from button
                if(this.tab === 'signing') {
                    if(this.boat_number){
                        let found_signee = this.searchBoatNumber(this.boat_number);
                        // If signee found
                        if(found_signee){
                            this.notification = `Venekunta lydetty!\n(${found_signee.boat_number}) : ${found_signee.captain_name}, ${found_signee.temp_captain_name}`;
                            this.selected_id = found_signee.id;
                            this.boat_number = found_signee.boat_number;
                            this.starting_place = found_signee.starting_place;
                            this.captain_name = found_signee.captain_name;
                            this.temp_captain_name = found_signee.temp_captain_name;
                            this.locality = found_signee.locality;
                            this.team = found_signee.team;                   
                        }
                        else {
                            // Nothing found
                            let temp_boat_number = this.boat_number;
                            this.clearInputs();
                            this.boat_number = temp_boat_number;
                            this.notification = "Tll numerolla ei ole viel ilmoitettu venekuntaa!"
                            this.selected_id = null;
                        }
                    }
                    else {
                        this.notification = `Syt venekunnan numero ennen hakemista!`;
                    }
                }

                else {
                    // Searched from Signees tab (this.tab === 'signees'), from table
                    if(this.selected_id){
                        let search_id = this.selected_id;
                        // Signee found based on id
                        if(search_id){
                            var found_signee = this.$store.getters.getSigneeById(search_id);
                            
                            if (found_signee){
                                this.notification = `Venekunnan tiedot:\n(${found_signee.boat_number}) : ${found_signee.captain_name}, ${found_signee.temp_captain_name}`;
                                this.boat_number = found_signee.boat_number;
                                this.starting_place = found_signee.starting_place;
                                this.captain_name = found_signee.captain_name;
                                this.temp_captain_name = found_signee.temp_captain_name;
                                this.locality = found_signee.locality;
                                this.team = found_signee.team; 
                                this.tab = 'signing';
                            }
                            else {
                                // Nothing found
                                let temp_boat_number = this.boat_number;
                                this.clearInputs();
                                this.boat_number = temp_boat_number;
                                this.notification = "Tll numerolla ei ole viel ilmoitettu venekuntaa!"
                                this.selected_id = null;
                            }
                        }
                        // Signee not found --> somehow selected signee from table that doesn't exist
                        else {
                            this.showError("Tt venekuntaa ei ole olemassa!?");
                        }
                    }
                    // No signee selected
                    else {
                        this.showError("Valitse ilmoittautuneideiden listalta venekunta ennen hakemista!");
                    }

                }
            },
            async searchFromCup() {
              this.selected_id = null;
              if(this.boat_number){
                 let found_signee = this.cup.signees.find(signee => {
                    return (parseInt(this.boat_number) === parseInt(signee.boat_number));
                });
                //If the signee is not found on cup, add it
                if (found_signee) {
                    this.notification = "Cupista lydetty seuraavat tiedot:"
                    this.captain_name = found_signee.captain_name;
                    this.temp_captain_name = found_signee.temp_captain_name;
                    this.locality = found_signee.locality;
                    this.team = found_signee.team;
                    this.starting_place = found_signee.starting_place;  
                }
                else {
                  // Nothing found
                  let temp_boat_number = this.boat_number;
                  this.clearInputs();
                  this.boat_number = temp_boat_number;
                  this.notification = "Tll numerolla ei lydy cupista venekuntaa!"
                }
              }
              else {
                this.notification = "Syt venekunnan numero ennen hakemista!";
              }
              
            },
            // Select row from table, if selected --> unselect
            // selected_id bound to selected css class (on App.vue)
            selectRow:function(id){
                if(id == this.selected_id){
                    this.selected_id = null;
                }
                else {
                    this.selected_id = id;
                }
            },
            // Fetch signee from vuex based on boat number
            // Check client\src\store\index.js for implementation
            searchBoatNumber: function(boat_number) {
                return this.$store.getters.getSigneeByBoatNumber(parseInt(boat_number));
            },
            // Fetch signee from vuex based on id
            // Check client\src\store\index.js for implementation
            searchId: function(id) {
                var search_id = parseInt(id);
                return this.$store.getters.getSigneeById(search_id);
            },
            // Utility function for showing error
            showError: function(error) {
                this.errors.push(error);
                M.toast({html: error});
                location.href = "#";
                location.href = "#app";
            },
             // Save signee to database
            async saveToDatabase(new_signee, replace) {
                // if replace == true, replace existing info, otherwise add new signee
                replace === true ? this.$store.commit('replaceSignee', new_signee) : this.$store.commit('addSignee', new_signee);
                 let index = this.cup.signees.findIndex(signee => {
                    return (parseInt(new_signee.boat_number) === parseInt(signee.boat_number));
                });
                //If the signee is not found on cup, add it
                if (index === -1) {
                  console.log("Not found on cup!");
                  this.cup.signees.push({
                    boat_number: new_signee.boat_number,
                    captain_name: new_signee.captain_name,
                    temp_captain_name: new_signee.temp_captain_name,
                    locality: new_signee.locality,
                    starting_place: new_signee.starting_place
                  })
                }
                // Create competition object
                let comp = this.$store.getters.getCompetition;
                this.signees = this.$store.getters.getSignees;
                comp.signees = this.signees;
                comp.state = "Ilmoittautuminen";
                // Store to vuex
                this.$store.commit('refreshCompetition', comp);
                try{
                  this.loading = true;
                  // Store to database
                  await CompetitionService.updateCompetition(comp._id, comp);
                  this.loading = false;
                  // Update signees to cup
                  await CupService.updateCup(comp.cup_id, this.cup);
                } catch(err) {
                  console.error(err.message);
                }
            },
            // Delete signee from vuex and database
            async deleteSignee(confirmed, id) {
                this.errors = [];
                
                if(!confirmed) {
                  if (!this.boat_number) {
                      this.showError('Venekunnan kilpailunumero puuttuu!');
                  }
                  if(!this.errors.length) {
                      // If selected on the signees list or with the search button
                      if(this.selected_id) {
                        // If id on signees
                        let found_signee = this.searchId(this.selected_id);
                        if(found_signee){
                          // Ask confirmation
                          this.$confirm(`Oletko varma ett haluat poistaa kilpailijan: (${found_signee.boat_number}), ${found_signee.captain_name}?`, "Poista kilpailija", 'question')
                          .then((r) => {
                              if(r) {
                                  // Deletion confirmed by user, call function again with confirmed == true
                                  this.deleteSignee(r, found_signee.id);
                              }
                          })
                          .catch((error) => {
                              if(error){
                                  console.error(error);
                              }
                          });
                        }
                      }
                      else {
                          // No selected id, so new input
                          let temp_boat = this.searchBoatNumber(this.boat_number);
                          if(temp_boat) {
                              this.$confirm(`Oletko varma ett haluat poistaa kilpailijan: (${temp_boat.boat_number}), ${temp_boat.captain_name}?`, "Poista kilpailija", 'question')
                              .then((r) => {
                                  if(r) {
                                      // Deletion confirmed by user, call function again with confirmed == true
                                      this.deleteSignee(r, temp_boat.id);
                                  }
                              })
                              .catch((error) => {
                                  if(error){
                                      console.error(error);
                                  }
                              });
                          }
                      }
                  }
                }
                // Deletion confirmed, delete signee
                else {
                    // Double check that correct user is selected
                    let found_signee = this.searchId(id);
                    if(found_signee){
                      // Remove from vuex
                      this.$store.commit("removeSignee", found_signee);
                      // Create competition object and update values to vuex and database
                      let comp = this.$store.getters.getCompetition;
                      //TODO only update signees array to database?
                      this.signees = this.$store.getters.getSignees;
                      comp.signees = this.signees;
                      this.$store.commit('refreshCompetition', comp);
                      try{
                        this.loading = true;
                        await CompetitionService.updateCompetition(comp._id, comp);
                        this.loading = false;
                        this.clearInputs();
                        M.toast({html: `Poistettu (Nro: ${found_signee.boat_number}, Kapteeni: ${found_signee.captain_name}) Tiedot!`});
                        location.href = "#";
                        location.href = "#signing";
                      } catch(err) {
                        console.log(err.message);
                      } 
                    }
                    else {
                      console.log("Tll id:ll ei lytynyt kilpailijaa...");
                    }
                }
            },
            // TODO serverside input validation
            validateInfo: function() {
                this.notification = null;
                this.errors = [];

                // Check all the inputs
                if (!this.boat_number) {
                    this.showError('Venekunnan kilpailunumero puuttuu!');
                }

                if (!this.captain_name) {
                    this.showError('Kapteenin nimi puuttuu!');
                }
                else {
                  this.captain_name = this.capitalize_words(this.captain_name);
                }

                if (!this.temp_captain_name) {
                    this.temp_captain_name = "-";
                }
                else {
                  this.temp_captain_name = this.capitalize_words(this.temp_captain_name);
                }

                if(!this.starting_place) {
                    this.starting_place = "-";
                }
                if(!this.team) {
                    this.team = "-";
                }

                if (this.team.length > 40) {
                  let temp_team = this.team
                  temp_team = temp_team.slice(0, 40);
                  this.team = temp_team; // Set the name back, shortened
                  this.showError("Valitse tiimin nimeksi alle 40 merkki pitk nimi (nimi lyhennetty 40 merkkiin, valittavissa pudotusvalikosta)!");
                }

                if (!this.locality) {
                    this.showError('Seura/Paikkakunta puuttuu!');
                }
                else {
                  this.locality = this.capitalize_words(this.locality);
                }
 
                // If no errors, proceed
                if(!this.errors.length) {
                    // If selected on the signees list or with the search button
                    if(this.selected_id) {
                      // If id on signees
                      let found_signee = this.searchId(this.selected_id);
                      if (found_signee) {
                          let temp_boat = this.searchBoatNumber(this.boat_number);
                          // If there already exist a boat with same number, but it isn't the same id
                          if(temp_boat && (temp_boat.id != found_signee.id)){
                            console.log("Boat number already exists...");
                            // Ask if user wants to overwrite
                            this.old_info = temp_boat;
                          }
                          else {
                              // If there isn't any boats with this boat number
                              found_signee.boat_number = parseInt(this.boat_number);
                              found_signee.starting_place = this.starting_place;
                              found_signee.captain_name = this.captain_name;
                              found_signee.temp_captain_name = this.temp_captain_name;
                              found_signee.locality = this.locality;
                              found_signee.team = this.team;
                              this.new_signee = found_signee;
                              // Save to database and vuex
                              this.saveToDatabase(this.new_signee, true);
                              if(this.teams.indexOf(this.team) == -1) {
                                this.teams.push(this.team);
                                this.$store.commit('setTeams', this.teams);
                              }
                              M.toast({html: `Pivitetty venekunnan (Nro: ${this.new_signee.boat_number}, Kapteeni: ${this.new_signee.captain_name}) Tiedot!`});
                              this.clearInputs();
                              location.href = "#";
                              location.href = "#signing";
                              // Update values for next signee
                              this.boat_number = Math.max.apply(Math, this.signees.map(function(o) { return o.boat_number; })) + 1;
                              this.id = Math.max.apply(Math, this.signees.map(function(o) { return o.id; })) + 1;
                              this.selected_id = null;
                          }
                      }
                    }
                    else {
                        // No selected id, so new input
                        let temp_boat = this.searchBoatNumber(this.boat_number)
                        // IF there is boat with same boat number, and somehow with this id
                        if(temp_boat && (temp_boat.id != this.id)){
                          console.log("Boat number already exists...");
                          this.notification =`Numerolla on jo olemassa venekunta, pllekirjoitetaanko?`;
                          this.old_info = temp_boat;
                        }
                        else {
                            // Otherwise create new signee
                            let competition_fishes =  this.$store.getters.getCompetitionFishes;
                            let weights = [];
                            // Init fish specific points and weights for signee
                            competition_fishes.forEach(element => {
                                let fish = {
                                  name: element.name,
                                  weights: 0,
                                  points: 0,
                                }
                                weights.push(fish);
                            });
                            // Create signee object and save to database/vuex
                            this.new_signee = {
                                id: this.id,
                                boat_number: parseInt(this.boat_number),
                                starting_place: this.starting_place,
                                captain_name: this.captain_name,
                                temp_captain_name: this.temp_captain_name,
                                locality: this.locality,
                                team: this.team,
                                total_points: 0,
                                total_weights: 0,
                                returned: false,
                                weights: weights
                            };
                            this.saveToDatabase(this.new_signee, false);

                            if(this.teams.indexOf(this.team) == -1) {
                              this.teams.push(this.team);
                              this.$store.commit('setTeams', this.teams);
                            }

                            M.toast({html: `Venekunta ilmoitettu kisaan! (Nro: ${this.new_signee.boat_number}, Kapteeni: ${this.new_signee.captain_name})`});
                            this.clearInputs();
                            location.href = "#";
                            location.href = "#signing";
                            // Update values for next signee
                            this.boat_number = Math.max.apply(Math, this.signees.map(function(o) { return o.boat_number; })) + 1;
                            this.id = Math.max.apply(Math, this.signees.map(function(o) { return o.id; })) + 1;
                            this.selected_id = null;
                        }                  
                    }
                }
            },
        },
}
</script>
<style>
#old-info-container {
    border: 1px solid black;
}

#new-info-container {
    border: 1px solid black;  
}
</style>